* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  overflow: hidden;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

/* iOS风格的聊天界面 */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 400px;
  margin: 0 auto;
  background: #000;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.chat-header {
  background: rgba(28, 28, 30, 0.8);
  backdrop-filter: blur(10px);
  padding: 15px 20px;
  text-align: center;
  border-bottom: 1px solid rgba(84, 84, 88, 0.3);
}

.chat-title {
  color: #fff;
  font-size: 17px;
  font-weight: 600;
  margin: 0;
}

/* 错误提示横幅 */
.error-banner {
  background: rgba(255, 59, 48, 0.9);
  color: white;
  padding: 8px 12px;
  margin-top: 8px;
  border-radius: 8px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  animation: slideIn 0.3s ease-out;
}

.error-text {
  flex: 1;
}

.error-close {
  font-size: 16px;
  font-weight: bold;
  margin-left: 8px;
  opacity: 0.8;
}

.error-banner:hover {
  background: rgba(255, 59, 48, 1);
}

@keyframes slideIn {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background: #000;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.chat-messages::-webkit-scrollbar {
  display: none;
}

.message {
  margin-bottom: 15px;
  display: flex;
  align-items: flex-start;
}

.message.user {
  flex-direction: row-reverse;
}

.message-bubble {
  max-width: 280px;
  padding: 12px 16px;
  border-radius: 18px;
  font-size: 16px;
  line-height: 1.4;
  word-wrap: break-word;
  position: relative;
}

.message.user .message-bubble {
  background: #007AFF;
  color: white;
  border-bottom-right-radius: 4px;
  margin-left: auto;
}

.message.ai .message-bubble {
  background: rgba(28, 28, 30, 1);
  color: white;
  border: 1px solid rgba(84, 84, 88, 0.3);
  border-bottom-left-radius: 4px;
}

.message-avatar {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  margin: 0 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 600;
}

.message.user .message-avatar {
  background: #007AFF;
  color: white;
}

.message.ai .message-avatar {
  background: #30D158;
  color: white;
}

.chat-input-container {
  padding: 20px;
  background: rgba(28, 28, 30, 0.8);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(84, 84, 88, 0.3);
}

.chat-input-wrapper {
  display: flex;
  align-items: flex-end;
  background: rgba(58, 58, 60, 1);
  border-radius: 20px;
  padding: 8px;
  min-height: 44px;
  position: relative;
}

.chat-input-wrapper.disabled {
  opacity: 0.6;
}

.chat-input {
  flex: 1;
  background: none;
  border: none;
  outline: none;
  color: white;
  font-size: 16px;
  padding: 8px 12px;
  resize: none;
  max-height: 120px;
  font-family: inherit;
}

.chat-input::placeholder {
  color: rgba(235, 235, 245, 0.6);
}

.chat-input:disabled {
  color: rgba(235, 235, 245, 0.4);
}

.send-button {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  background: #007AFF;
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
}

.send-button:hover:not(:disabled) {
  background: #0056CC;
}

.send-button:disabled {
  background: rgba(120, 120, 128, 0.3);
  cursor: not-allowed;
}

.typing-indicator {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  color: rgba(235, 235, 245, 0.6);
  font-size: 14px;
}

.typing-dots {
  display: flex;
  margin-left: 8px;
}

.typing-dot {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: rgba(235, 235, 245, 0.6);
  margin: 0 1px;
  animation: typing 1.4s infinite ease-in-out;
}

.typing-dot:nth-child(1) {
  animation-delay: -0.32s;
}

.typing-dot:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes typing {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Markdown样式 */
.message-content {
  word-break: break-word;
}

.message-content p {
  margin: 0 0 8px 0;
}

.message-content p:last-child {
  margin-bottom: 0;
}

/* 代码块样式 */
.code-block {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  padding: 12px;
  margin: 8px 0;
  overflow-x: auto;
  font-size: 14px;
  border: 1px solid rgba(84, 84, 88, 0.3);
}

.code-block code {
  background: none;
  padding: 0;
  font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
  color: #fff;
  white-space: pre;
}

/* 行内代码样式 */
.message-content code {
  background: rgba(0, 0, 0, 0.3);
  padding: 2px 4px;
  border-radius: 4px;
  font-size: 14px;
  font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
}

.message-content pre code {
  background: none;
  padding: 0;
}

.message-content blockquote {
  border-left: 3px solid rgba(235, 235, 245, 0.3);
  padding-left: 12px;
  margin: 8px 0;
  color: rgba(235, 235, 245, 0.8);
}

.message-content ul, .message-content ol {
  padding-left: 20px;
  margin: 8px 0;
}

.message-content li {
  margin: 4px 0;
}

.message-content strong {
  font-weight: 600;
}

.message-content em {
  font-style: italic;
}

.message-content h1, .message-content h2, .message-content h3,
.message-content h4, .message-content h5, .message-content h6 {
  margin: 12px 0 8px 0;
  font-weight: 600;
}

.message-content a {
  color: #007AFF;
  text-decoration: none;
}

.message-content a:hover {
  text-decoration: underline;
}

.time-stamp {
  font-size: 11px;
  color: rgba(235, 235, 245, 0.6);
  margin-top: 4px;
  text-align: center;
}

/* 加载状态 */
.loading-indicator {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: rgba(235, 235, 245, 0.6);
  font-size: 14px;
}

.loading-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(235, 235, 245, 0.2);
  border-top: 2px solid rgba(235, 235, 245, 0.6);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 响应式设计 */
@media (max-width: 480px) {
  .chat-container {
    max-width: 100%;
    height: 100vh;
    border-radius: 0;
  }
  
  .message-bubble {
    max-width: calc(100vw - 120px);
  }
}

/* 状态指示器 */
.connection-status {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #30D158;
}

.connection-status.disconnected {
  background: #FF3B30;
}

.connection-status.connecting {
  background: #FF9500;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}